import React, { useEffect, useState } from "react";

import { filterStartAndEndDate } from "./DatePicker.utils";
import { type DatePickerFooterProps } from "./DatePickerFooter";
import DatePickerPopover, { type DatePickerPopoverProps } from "./DatePickerPopover";
import { DatePickerShortcutsPanelProps } from "./DatePickerShortcutsPanel";
import DateRangePickerContent from "./DateRangePickerContent";
import { DateRangePickerInputProps } from "./DateRangePickerInput";
import { StaticDateRangePickerProps } from "./StaticDateRangePicker";

type ExtendedProps = Pick<DatePickerPopoverProps, "trigger" | "$zIndex">
	& Pick<DatePickerShortcutsPanelProps, "shortcuts">
	& Pick<DateRangePickerInputProps, "inputLeftIcon" | "inputRightIcon">;

export type FooterProps = Pick<DatePickerFooterProps, "showFooterInputs"> & {
	/**
	 * @default true
	 * @description This will always show the footer with the Cancel and Apply buttons
	 **/
	showFooter?: boolean
};

export type DateRangePickerProps = ExtendedProps & {
	staticDateRangePickerProps: StaticDateRangePickerProps;
	footerProps?: FooterProps;
	dateFormat?: string;
	placeholder?: string;
	label?: string,
	/**
	 * @default false
	 */
	closeOnDateSelected?: DatePickerPopoverProps["closeOnDateSelected"]
};

const DateRangePicker = ({
	staticDateRangePickerProps,
	trigger,
	closeOnDateSelected = false,
	footerProps,
	shortcuts,
	dateFormat,
	placeholder,
	inputLeftIcon,
	inputRightIcon,
	label,
	$zIndex,
}: DateRangePickerProps) => {
	const defaultFooterProps: FooterProps = {
		showFooter: true,
		...footerProps,
	};

	// state is stored here so that the DateRangePickerInput can get access
	const [selectedDates, setSelectedDates] = useState<Date[]>([]);

	useEffect(() => {
		const filteredStartAndEndDate = filterStartAndEndDate(
			[staticDateRangePickerProps.startDate, staticDateRangePickerProps.endDate],
		);
		setSelectedDates(filteredStartAndEndDate);
	}, [staticDateRangePickerProps.endDate, staticDateRangePickerProps.startDate]);

	const getDatesSelected = () => {
		const filteredStartAndEndDate = filterStartAndEndDate(
			[staticDateRangePickerProps.startDate, staticDateRangePickerProps.endDate],
		);
		// show the selectedDates in case there is a change if Apply has already been clicked
		if (selectedDates.length > 0) {
			return selectedDates;
		}

		if (filterStartAndEndDate.length > 0) {
			return filteredStartAndEndDate;
		}

		return [];
	};

	return (
		<DatePickerPopover
			dataQA={`${staticDateRangePickerProps.dataQA}-popover`}
			trigger={trigger}
			closeOnDateSelected={closeOnDateSelected}
			datesSelected={getDatesSelected()}
			minDate={staticDateRangePickerProps.dayzedProps?.minDate}
			maxDate={staticDateRangePickerProps.dayzedProps?.maxDate}
			onDateChange={(dates) => dates instanceof Array && staticDateRangePickerProps.onDateChange(dates)}
			isRange
			inputLeftIcon={inputLeftIcon}
			inputRightIcon={inputRightIcon}
			label={label}
			placeholder={placeholder}
			$zIndex={$zIndex}
		>
			<DateRangePickerContent
				staticDateRangePickerProps={staticDateRangePickerProps}
				footerProps={defaultFooterProps}
				shortcuts={shortcuts}
				dateFormat={dateFormat}
				placeholder={placeholder || dateFormat}
				selectedDates={selectedDates}
				setSelectedDates={setSelectedDates}
			/>
		</DatePickerPopover>
	);
	 };

export default DateRangePicker;
